body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.addAlbumForm{
  display: flex;
  flex-direction: column;
  align-content: center;
  flex-wrap: wrap;
  align-items: baseline;
}
.addAlbumForm input{
  width: 50%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  background-color: #fff0f0c2;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
.addAlbumForm input[readOnly]{
  background-color: #f77474;
  cursor: not-allowed;
}
.addAlbumForm label{
  display: block;
}
.addAlbumForm button{
  background-color: #047baa; 
  color: white; 
  border: none;
  border-radius: 5%;
  display: block;
  padding: 8px 10px;
  text-align: center;
  font-size: 18px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin: 5rem;
  max-width: 100%; 
  width: 90%; 
  height: 400px;
  overflow-y: auto;
}

.showAlbum {
  border-collapse: collapse;
  margin: 25px 0;
  font-size: 0.9em;
  font-family: sans-serif;
  min-width: 100%; 
  position: relative;
}

.addAlbumButton {
  border: 1px solid #000;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  background-color: #67fddf;
  line-height: 1.5;
  padding: 12px 16px;
  text-decoration: none;
  text-overflow: ellipsis;
  transition: all 0.14s ease-out;
}

.addAlbumButton:hover {
  box-shadow: 4px 4px 0 #000;
  transform: translate(-4px, -4px);
}

.albumTable {
  border-collapse: collapse;
  margin: 25px 0;
  font-size: 0.9em;
  font-family: sans-serif;
  min-width: 100%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.albumTable thead {
  position: sticky;
  top: 0;
  background-color: #009879;
  color: #ffffff;
  z-index: 1;
}

.albumTable thead tr {
  background-color: #009879;
  color: #ffffff;
  font-size: large;
}

.albumTable th,
.albumTable td {
  padding: 12px 15px;
}

.albumTable tbody tr {
  border-bottom: 1px solid #dddddd;
}

.albumTable tbody tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

.albumTable tbody tr:last-of-type {
  border-bottom: 2px solid #009879;
}

.albumTable tbody button{
  padding: 6px 8px;
  text-align: center;
  font-size: 14px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
}

.updateButton{
  background-color: white; 
  color: black; 
  border: 2px solid #047baa
}

.updateButton:hover{
  background-color: #047baa;
  color: white;
}

.deleteButton{
  background-color: white; 
  color: black; 
  border: 2px solid #fa3636
}

.deleteButton:hover{
  background-color: #fa3636;
  color: white;
}
.updateForm{
  display: flex;
  flex-direction: column;
  align-content: center;
  flex-wrap: wrap;
  align-items:center;
}

.updateForm input{
  width: 50%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  background-color: #fff0f0c2;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.updateForm button{
  background-color: #047baa; 
  color: white; 
  border: none;
  border-radius: 5%;
  display: block;
  padding: 8px 10px;
  text-align: center;
  font-size: 18px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
}